<style lang="less" scoped>
  .home {
    height: 100%;
    padding: 20px;
    background: #EEF2F8;
  }
  .home-top {
    display: flex;
    font-size: 14px;
    margin-bottom: 20px;
    .home-personal {
      width: 355px;
      height: 300px;
      background: #fff;
      padding: 35px;
      flex-shrink: 0;
      margin-right: 20px;
      .home-personal-top {
        display: flex;
        border-bottom: 1px solid #f2f2f2;
        padding-bottom: 28px;
        margin-bottom: 28px;
        .userinfo {
          flex-grow: 1;
          h1 {
            font-size: 20px;
            margin: 0 0 16px;
            font-weight: normal;
            line-height: 20px;
            span {
              font-size: 12px;
              color: #FFAA00;
              display: inline-block;
              padding: 0 10px;
              height: 20px;
              border: 1px solid #FFAA00;
              border-radius: 20px;
              margin-left: 20px;
              vertical-align: top;
            }
          }
          p {
            line-height: 20px;
            color: #808080;
            margin-top: 4px;
            span {
              color: #398cff;
            }
          }
        }
        .user-avatar {
          width: 88px;
          height: 88px;
          line-height: 88px;
          border-radius: 50%;
          overflow: hidden;
          img {
            max-width: 100%;
            vertical-align: middle;
          }
        }
      }
      .home-personal-bottom {
        p {
          color: #666;
          line-height: 28px;
          span {
            color: #333;
          }
          &:last-child {
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
          }
        }
      }
    }
  }
  .approval {
    width: 100%;
    background: #fff;
    padding: 0 36px;
    .approval-title {
      padding: 24px 0;
      span {
        display: inline-block;
        font-size: 20px;
        &::before {
          content: '';
          width: 4px;
          height: 16px;
          background: #398cff;
          display: inline-block;
          margin-right: 16px;
        }
      }
      a {
        float: right;
        color: #398cff;
        font-size: 14px;
      }
    }
    .approval-list {
      @{global} .el-card__body {
        padding: 16px 0;
      }
      a {
        display: flex;
        align-items: center;
        justify-content: space-around;
        span {
          color: #666;
          &:first-child {
            color: #333;
          }
          &:last-child {
            color: #999;
            font-size: 12px;
          }
        }
      }
    }
  }
  .sysList.el-row {
    @{global} .el-card {
      margin-bottom: 24px;
      .el-card__body {
        display: flex;
        align-items: center;
        .icon {
          margin-right: 30px;
        }
        span {
          color: #666;
          font-size: 14px;
        }
      }
    }
  }
</style>
<template>
  <div class="home">
    <div class="home-top">
      <div class="home-personal">
        <div class="home-personal-top">
          <div class="userinfo">
            <h1>{{userProfile.user_name}}<span>在职</span></h1>
            <p>岗位: <span>{{userProfile.position}}</span></p>
            <p>部门: <span>{{userProfile.department_name}}</span></p>
          </div>
          <div class="user-avatar">
            <img :src="userProfile.avatar_url" alt="">
          </div>
        </div>
        <!--<div class="home-personal-bottom">-->
          <!--<p>您上次登录时间是：<span>{{userProfile.last_login_ip}}</span></p>-->
          <!--<p>您上次登录的IP是：<span>{{userProfile.last_logon_ip}}</span></p>-->
          <!--<p class="text-overflow-ellipsis">您上次登录的计算机名是：<span>{{userProfile.last_login_device}}</span></p>-->
        <!--</div>-->
      </div>
      <div class="approval">
        <div class="approval-title"><span>审批</span>
          <router-link to="/process-management/approval"><q style="vertical-align: middle;">查看更多</q> &gt;</router-link>
        </div>
        <div class="approval-list">
          <el-row :gutter="20">
            <el-col :span="8" v-for="(item, index) in approvalList" :key="index">
              <el-card shadow="hover">
                <router-link :to="{path: `/process-management/approval/${item.id}`}">
                  <span class="text-overflow-ellipsis">{{item.user_name}}</span>
                  <span class="text-overflow-ellipsis">{{item.content}}</span>
                  <span class="text-overflow-ellipsis">{{item.created_at | date}}</span>
                </router-link>
              </el-card>
            </el-col>
          </el-row>
        </div>
      </div>
    </div>
    <div class="approval">
      <div class="approval-title"><span>基本系统</span></div>
      <el-row :gutter="36" class="sysList">
        <el-col :span="6" v-for="(item, index) in cptBaseSys" :key="index">
          <a :href="item.url" target="_blank">
            <el-card shadow="hover">
              <svg class="icon" aria-hidden="true"><use :xlink:href="'#icon-'+(item.icon? item.icon: 'email')"></use></svg>
              <span>{{item.name}}</span>
            </el-card>
          </a>
        </el-col>
      </el-row>
    </div>
    <div class="approval">
      <div class="approval-title"><span>业务系统</span></div>
      <el-row :gutter="36" class="sysList">
        <el-col :span="6" v-for="(item, index) in cptBusinessSys" :key="index">
          <a :href="item.url">
            <el-card shadow="hover">
              <svg class="icon" aria-hidden="true"><use :xlink:href="'#icon-'+(item.icon? item.icon: 'email')"></use></svg>
              <span>{{item.name}}</span>
            </el-card>
          </a>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
import api from '../api'
export default {
  data () {
    return {
      email: '#icon-email',
      userProfile: {},
      associatedSystem: [],
      approvalList: []
    }
  },
  created () {
    this.Init()
  },
  computed: {
    // 1是基础系统， 2是业务系统
    cptBaseSys () {
      return this.associatedSystem.filter(item => this.$utils.isNotEqual(item.type, 2))
    },
    cptBusinessSys () {
      return this.associatedSystem.filter(item => this.$utils.isNotEqual(item.type, 1))
    }
  },
  methods: {
    closePwdWarning () {
      this.notify.close()
    },
    toRevisePwd () {
      this.$router.push('/resourceManage/sysManage')
      this.closePwdWarning()
    },
    Init () {
      this.userProfile = this.$store.state.userInfo || {}
      if (this.userProfile.reset_tips !== null && this.userProfile.reset_tips !== '' && this.userProfile.reset_tips !== undefined) {
        this.$notify({
          title: '密码即将到期',
          type: 'warning',
          message: this.userProfile.reset_tips,
          duration: 0
        })
      }
      api.getUserAssociatedSystem().then(res => {
        this.associatedSystem = res.result
      })
      api.getUserApprovalList({page: 1, per_page: 9}).then(res => {
        this.approvalList = res.result.data
      })
    }
  }
}
</script>



